@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Quicksand";
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.content {
  padding-top: 80px;
}
.main {
    width: 90%;
    margin-left: 5%;
    padding: 20px;
}

.news {
    width: 80%;
    height: 400px;
    margin-left: 10%;
    margin-bottom: 100px;
    display: flex;
    flex-direction: row;
}

.news:hover {
    -webkit-text-decoration: underline 5px black;
            text-decoration: underline 5px black;
}

a {
    text-decoration: none;
}

.news-info {
    width: 40%;
    height: 100%;
    background-color: rgb(197, 4, 4);
    color: white;
    font-size: larger;
}

.news-info h3{
    color: black;
}

.news-image {
    height: 100%;
    width: 100%;
}

.news-image img {
    height: 100%;
    width: 100%;
}

.news-feed {
    width: 80%;
    margin-left: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 100px;
    color: black;
}

.news-feed img {
    width: 100%;
    height: 100%;
}

.news1, .news2, .news3 {
    height: 200px;
    width: 32%;
}

.news1:hover, .news2:hover, .news3:hover {
    text-decoration: underline;
    color: black;
}

.videos {
    width: 80%;
    margin-left: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 100px;
}

.video1, .video2 {
    height: 250px;
    width: 48%;
}


.main-tickets {
    
}


.nav-container {
    width: 100%;
    position: fixed;
    z-index: 1;
    background-color: rgb(197, 4, 4);
    display: flex;
    font-size: 25px;
    align-items: baseline;
    box-shadow: 0 5px 8px 0 rgb(0 0 0 / 10%), 0 3px 10px 0 rgb(0 0 0 / 10%) !important;
}

.navbar {
    padding: 20px;
    display: flex;
    align-items: center;
    max-width: 1100px;
    max-height: 40px;
    margin: 0 auto;
  }

  .navbar .links {
    margin-left: auto;
    max-width: 600px;
    margin: 40px;
    padding: 20px;
  }
  .navbar a {
    margin-left: 25px;
    text-decoration: none;
    padding: 6px;
    color: #f2f2f2;
  }

  .navbar a:hover {
    background-color: #f2f2f2;
    border-radius: 7px;
    color: rgb(197, 4, 4);
  }

  .navbar img {
    height: 70px;
    width: 70px;
  }
.news-main {
    width: 80%;
    margin-left: 10%;
}

.team-image img{
    width: 70%;
}

.news-main h1 {
    font-size: 60px;
}

.news-main p {
    font-size: 20px;
    text-align: justify;
}

.news-text {
    width: 70%;
    margin-left: 15%;
}
